.todo-section div {
    margin: 40px auto;
    background: #fff;
    border-radius: 10px;
    padding: 32px 24px 24px 24px;
}

.todo-section h1 {
    text-align: center;
    color: #333;
    margin-bottom: 24px;
    font-size: 2rem;
    letter-spacing: 1px;
}

.todo-section form {
    display: flex;
    gap: 8px;
    margin-bottom: 20px;
}

.todo-section input[type="text"] {
    flex: 1;
    padding: 10px 12px;
    border: 1px solid #dcdde1;
    border-radius: 5px;
    font-size: 1rem;
    outline: none;
    transition: border 0.2s;
}

.todo-section input[type="text"]:focus {
    border: 1.5px solid #37ee4f;
}

.todo-section button {
    background: green;
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 0 18px;
    font-size: 1rem;
    cursor: pointer;
    transition: 0.3s;
}

.todo-section ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.todo-section li {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 0;
    border-bottom: 1px solid #f0f0f0;
}

.todo-section li:last-child {
    border-bottom: none;
}

.todo-section li span {
    flex: 1;
    font-size: 1rem;
    transition: color 0.2s;
}

.todo-section li span:hover {
    color: #4f8cff;
}

.todo-section .delete-btn {
    margin-left: 12px;
    background: #ff4d4f;
    padding: 4px 12px;
    font-size: 0.95rem;
    border-radius: 4px;
    transition: background 0.2s;
}

.todo-section .delete-btn:hover {
    background: #d90429;
    font-size: 1rem;
}

.todo-section .complete-btn {
    background-color: green;
    padding: 4px 12px;
    font-size: 0.95rem;
    border-radius: 4px;
    transition: background 0.2s;
}

.todo-section .complete-btn:hover {
    background-color: #37ee4f;
    font-size: 1rem;
}
