/* * footer.css */ 
.footer-container {
  background-color: teal; /* deep green */
  color: white;
  padding: 40px 20px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  text-align: center;
}

.footer-grid {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
}

.footer-icons {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
}

.footer-icons .icon {
  font-size: 28px;
  color: white;
  transition: color 0.3s ease;
}

.footer-icons .icon:hover {
  color: #86efac; /* soft green on hover */
  cursor: pointer;
}

.footer-description p {
  font-size: 16px;
  line-height: 1.6;
  max-width: 600px;
  margin: 0 auto;
}
