.prof {
  display: grid;
  /* grid-template-columns: repeat(auto-fit, minmax(280px, 1fr)); */
  gap: 1.5rem;
  grid-template-columns: repeat(2 , 1fr);
  width: 90%;
  margin: auto;
  margin-top: 1.5rem;
}

/* Personal info styling */
.personal-info {
  display: flex;
  flex-direction: column;
  align-items: self-start;
  gap: 1.5rem;
}

.profile-pic img {
  width: 120px;
  height: 120px;
  border-radius: 50%; /* makes it circular */
  object-fit: cover;
  border: 1px solid #4caf50;
}

.info-text h2 {
  margin: 0 0 0.5rem 0;
  color: #2e7d32;
}

.info-text p {
  margin: 0.2rem 0;
  color: #555;
}

/* Skills card styling */
.skills ul {
  list-style: none;
  padding: 0;
  margin: 0;
  text-align: start;
  
}

.skills ul li {
  margin-bottom: 0.7rem;
  
}

.skills ul li a {
  color: black;
  text-decoration: none;
  font-weight: 600;
  transition: color 0.3s;
}

.skills ul li a:hover {
  color: #4caf50;
  /* background-color: #81c784; */
  display: block;
  transition: color 0.3s;
  
}

/* Quote card styling */
.quote-card blockquote {
  font-style: italic;
  color: #555;
  border-left: 4px solid #4caf50;
  padding-left: 1rem;
  margin: 0;
  font-size: 1.1rem;
}
